// 메인화면

import { instance, instanceWithAuth } from './index';

function fetchTerm() {
	return instanceWithAuth.get('api/admin/term/all');
}

function fetchTermDetail(title) {
	return instance.get(`api/admin/term/detail/${title}`);
}

function fetchFooterPrivate() {
	return instance.get('api/term/footer/private_policy');
}

function fetchFooterUserTerm() {
	return instance.get('api/term/footer/term_of_use');
}

function updateTerm(id, data) {
	return instanceWithAuth.put(`api/admin/term/update/${id}`, data);
}

export {
	fetchTerm,
	fetchTermDetail,
	fetchFooterPrivate,
	fetchFooterUserTerm,
	updateTerm,
};
