<template>
	<div class="sub-container">
		<h2 class="board-title">이용 약관</h2>
		<div class="terms-content">
			<img class="web" :src="mobileImage.imgPath" />
			<img class="mobile" :src="webImage.imgPath" />
		</div>
		<LoadingSpinner :loading="isLoading" />
	</div>
</template>

<script>
import { fetchFooterUserTerm } from '@/api/terms';
import LoadingSpinner from '@/components/common/LoadingSpinner';

export default {
	components: { LoadingSpinner },
	created() {
		this.fetchFooterUserTerm();
	},
	data() {
		return {
			webImage: [],
			mobileImage: [],
			isLoading: false,
		};
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async fetchFooterUserTerm() {
			this.isLoading = true;
			try {
				const { data } = await fetchFooterUserTerm();
				this.webImage = data.filter(item => item.webMobile == 'WEB');
				this.mobileImage = data.filter(item => item.webMobile == 'MOBILE');
				this.webImage = this.webImage[0];
				this.mobileImage = this.mobileImage[0];
			} catch (e) {
				console.log(e);
			} finally {
				this.isLoading = false;
			}
		},
	},
};
</script>

<style scoped>
@media screen and (max-width: 600px) {
	.terms-content {
		width: 100%;
	}

	.terms-content img {
		width: 100%;
	}
	.mobile {
		display: block;
	}
	.web {
		display: none;
	}
}

@media screen and (min-width: 601px) {
	.terms-content {
		width: 700px;
		margin: 0 auto;
	}
	.terms-content img {
		width: 100%;
	}
	.mobile {
		display: none;
	}
	.web {
		display: block;
	}
}
</style>
